
.grid-body{
    position: relative;
    top:-365px;
    width: 100vw;
    margin: 0 auto 0 15px;
    /* overflow: hidden; */
    padding: 20px 0;
    display: flex;
    flex-flow: row wrap;
    gap: 20px;
  
}

.card{
    background-color: #fff;
    z-index: 3;
    max-width: 345px;
    height: 420px;
    min-height: 420px;
    padding-left: 10px;
    padding-right: 10px;
    min-width: 290px;
    padding-top: 20px;
    & > h3{
        margin-left: 10px;
        font-size: 1.3rem;
    }
}

.each-card{
    display: flex;
    flex-wrap: wrap;
    padding: 10px;
    gap: 20px;
    & img{
        width: 140px;
        height: 130px;
    }
    & p{
        font-size: 0.86rem;
    }
}

